@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2e2e2e;
  color:#ffffff;
  text-align: center;
}
input:focus{
  outline: none;
}
button:focus{
  outline: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}
.heading{
  font-weight: 600;
  font-size: 400%;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
.cusInput{
  background: #121212;
  border-radius: 15px;
  width: 30vw;
  height: 3vh;
  border: none;
  color:#ffffff;
  font-size: 2vh;
  padding: 1%;
  box-shadow: 5px 5px 20px  black;
}
.cusInput:focus{
  outline: none;
  border-radius: 15px;
  padding:1%;
}
.alert{
  padding: 1%;
  text-align: center;
  background: hsl(0, 58%, 59%);
  color: #ffffff;
  border-radius: 15px;
  width: 30vw;
  margin-left: 35%;
  box-shadow: 4px 4px 16px hsl(0, 87%, 44%);
  margin-top: 3%;
} 
.cusBut1{
  border-radius: 15px;
  width: 4vw;
  height: 70%;
  border: none;
  color:#ffffff;
  font-size: 2vh;
  font-weight: 600;
  padding: 0.6%;
  box-shadow: 3px 3px 9px #014b01;
  background:  #69bf78;
  margin-left:-2%;
}
.cusBut1:hover{
  box-shadow: 5px 5px 15px hsl(120, 95%, 30%);
  -webkit-transform: translate(0,-2px);
          transform: translate(0,-2px);
  -webkit-transition: 0.5ms all;
  transition: 0.5ms all;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.name{

}
.dataCapsule{
  margin: 2%;
  background: #121212;
  border-radius: 15px;
  border: none;
  color:#ffffff;
  font-size: 2vh;
  padding: 1%;
  box-shadow: 5px 5px 20px  black;
  display: inline;
  justify-content: center;
  align-content: center;
}
.icon{
  margin-right: 40%;
  margin-left: 40%;
  background: #353535;
  border-radius: 50%;
  justify-content: center;
  align-content: center;
}

